.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
h1{
  color: rgb(69, 212, 69);
  font-weight: bold;
}
.wapper{
  width: 500px; height: 650px;
  margin: 0 auto;
  border: 1px solid #ededed;
  position: relative;
  box-sizing: border-box; padding-top: 35px;
}
.wapper>.title{
  font-size: 25px;
  font-weight: 400;
}
.wapper>.title_memo{
  font-size: 15px;
  color: lightgray;
}
button{
  width: 450px; height: 50px;
}
.login_btn1{
  background: lightgrey;
  font-size: 16px; color: white;
  border: 1px solid lightgray;
  font-weight: bold;
  margin-top: 15px;
  margin-bottom: 10px;
}
.wapper>a{
  width: 150px; height: 15px;
  position: absolute;
  top: 280px; right: 12px;
  color: black;
}
input{
  width: 450px; height: 50px;
  border-bottom: 1px solid #ededed;
  border-top: none; border-left: none; border-right: none;
}
.login_bottom{
  margin-top: 50px;
}
.login_bottom>p{
  color: lightgray;
}
.login_bottom>button{
  background: white; 
  border: 1px solid lightgrey;
}
.login_btn2{
  margin-bottom: 15px;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
